import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import * as classes from './ControlSectionSpace.css.js';
import * as actions from '../../../../store/actions/index';
import { dragStart } from '../../../../utils/dragFunction';

export const ControlSectionSpace = props => {
    const handleDragMouseDown = (e) => {
        const startElY = props.section.space;

        dragStart(e, (difX, difY) => {
            let value = startElY + difY;
            if (value < 0) {
                value = 0;
            }
            props.changed(value, 'space');
        }, () => {});
    };

    return !props.previewIsOn ?
        <div style={classes.SectionSpace}
            onMouseDown={handleDragMouseDown}>
            Space
        </div>
        : null;
};

ControlSectionSpace.propTypes = {
    section: PropTypes.shape({
        type: PropTypes.oneOf(['section']).isRequired,
        height: PropTypes.number.isRequired,
        children: PropTypes.arrayOf(PropTypes.string).isRequired,
        styles: PropTypes.arrayOf(PropTypes.string).isRequired
    }).isRequired,
    id: PropTypes.string.isRequired
};

const mapStateToProps = (state, ownProps) => {
    return {
        section: state.builder.present[ownProps.id],
        previewIsOn: state.builder.present.preview        
    };
};

const mapDispatchToProps = dispatch => {
    return {
        changed: (newValue, valueKey) => dispatch(actions.changeElementPropertyValue(newValue, valueKey, true))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ControlSectionSpace);